.section_1 {
    position: relative;
    height: 800px;
    color: #fff;
    background: url(images/graphics/bg-banner.jpg) no-repeat center;
    background-size: cover;

    &::before {
        content: "";
        background: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        z-index: 1;
    }

    .container {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
    }

    .logo {
        display: block;
        text-align: center;
        margin-top: 50px;

        img {
            width: 170px;
        }
    }

    .content {
        max-width: 960px;
        margin: 80px auto 0;
        text-align: center;

        p {
            margin-bottom: 60px;
            line-height: 1.6;
        }

        .title {
            font-size: 20px;
            text-transform: uppercase;
            margin-bottom: 25px;
        }
    }

    .social {
        text-align: center;
        position: absolute;
        bottom: 25px;
        left: 0;
        right: 0;

        a {
            color: var(--orange);
            margin: 0 10px;
            font-size: 22px;
        }
    }

    .box-countdown {
        margin-bottom: 24px;

        .flex {
            justify-content: center;
        }

        .item_countdown {
            padding: 0 46px;
            margin-bottom: 24px;
            position: relative;

            &::before {
                content: "";
                position: absolute;
                width: 3px;
                height: 33px;
                background: #fff;
                right: 0;
                top: 8px;
            }

            .item_txt {
                span {
                    font-size: 50px;
                    line-height: 1;
                    width: 60px;
                    display: block
                }

                .text {
                    font-size: 16px;
                    color: var(--orange);
                }
            }

            &.giay {
                &::before {
                    display: none;
                }
            }
        }
    }

    .email_subcribe {
        .search {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }

        .input_form {
            background: rgba(20, 20, 20, 0.5);
            border: 1px solid #FFDFA4;
            border-radius: 2px;
            font-size: 14px;
            padding: 10px;
            color: #fff;
            width: 300px;
            height: 42px;

            &:focus {
                box-shadow: none;
                outline: 0;
            }
        }

        button {
            background: #FFDFA4;
            border-radius: 2px;
            color: #000000;
            font-size: 16px;
            outline: 0;
            height: 42px;
            padding: 10px;
            margin-left: 10px;
        }
    }

    @media (min-width: 1700px) {
        height: 100vh;

        .logo {
            margin-top: 80px;
        }

        .content {
            margin: 150px auto 0;
        }
    }

    @media (max-width: 991px) {
        height: 650px;
        font-size: 14px;

        .container {
            max-width: 100%;
            padding: 0 20px;
        }

        .logo {
            margin-top: 30px;

            img {
                width: 120px;
            }
        }

        .content {
            .title {
                font-size: 18px
            }
        }

        .box-countdown {
            margin-bottom: 0;

            .item_countdown {
                padding: 0 15px;

                &::before {
                    width: 2px;
                    height: 20px;
                }

                .item_txt {
                    span {
                        font-size: 30px;
                        width: 40px;
                    }

                    .text {
                        font-size: 14px;
                    }
                }
            }
        }
    }

    @media (max-width: 576px) {
        height: 650px;
        background: url(images/graphics/bg-banner.png) no-repeat center;
        background-size: cover;

        .logo {
            margin-top: 40px;
        }

        .content {
            margin-top: 30px;

            p {
                text-align: justify;
            }
        }

        .email_subcribe {
            .input_form {
                width: 180px;
                padding: 0 5px;
                height: 33px;
                font-size: 13px;
            }

            button {
                font-size: 13px;
                padding: 0 5px;
                height: 33px;
            }
        }

        .box-countdown {
            .item_countdown {
                margin-bottom: 20px;

                .item_txt {
                    span {
                        font-size: 22px;
                        width: 30px;
                    }
                }
            }
        }

        .social {
            bottom: 30px;

            a {
                font-size: 18px;
            }
        }
    }

    @media (max-width: 360px) {
        height: 700px;

        .social {
            bottom: 15px
        }

        .box-countdown {
            .item_countdown {
                padding: 0 10px;
            }
        }

        .email_subcribe {
            .input_form {
                width: 100%;
            }

            button {
                width: 100%;
                margin: 10px 0 0 0;
            }
        }
    }
}

.section_2 {
    color: #fff;
    padding: 50px 0;

    .img {
        width: 690px;
        width: calc(100% - 450px);
        padding-right: 34px;

        ul {
            display: flex;
            flex-wrap: wrap;

            li {
                width: 374px;
                padding: 0 5px;

                .thumb {
                    margin-bottom: 10px;
                }

                .thumb_5x3 {
                    padding-bottom: 54.5%;
                }

                &:nth-child(2) {
                    width: 282px;
                }
            }
        }

        .swiper-container {
            display: none;
            margin-bottom: 35px;
            padding-right: 100px;
            margin-right: -15px;
        }
    }

    .content {
        width: 450px;
        padding-top: 34px;

    }

    @media (max-width: 1140px) {
        .img {
            width: 500px;

            ul {
                li {
                    width: 250px;

                    .thumb_5x3 {
                        padding-bottom: 62.2%;
                    }

                    &:nth-child(2) {
                        width: 216px;
                    }
                }
            }
        }

        .content {
            width: calc(100% - 500px);
            padding-top: 0;
        }
    }

    @media (max-width: 991px) {
        .img {
            width: 100%;
            padding-right: 0;

            ul {
                li {
                    width: 55%;

                    .thumb_5x3 {
                        padding-bottom: 59.7%;
                    }

                    &:nth-child(2) {
                        width: 45%;
                    }
                }
            }
        }

        .content {
            width: 100%;
            padding-top: 0px;
        }
    }

    @media (max-width: 576px) {
        padding: 30px 0;

        .img {
            ul {
                display: none;
            }

            .swiper-container {
                display: block;
            }
        }
    }
}

.title-cate {
    color: #FFDFA4;
    font-size: 36px;
    margin-bottom: 20px;

    @media (max-width: 1140px) {
        font-size: 26px
    }

}

.section_3 {
    position: relative;
    color: #fff;
    padding: 50px 0;
    background: url(images/graphics/bg-s3.png) no-repeat center;
    background-size: cover;

    .content {
        text-align: center;
        width: 100%;
        padding: 0 20px;
        position: relative;
        font-size: 20px;

        &::before {
            content: "\f10d";
            font: normal normal normal 30px/1 FontAwesome;
            color: #FFDFA4;
            margin-bottom: 20px;
            display: block;
        }
    }

}

.section_4 {
    padding-top: 30px;
    margin-bottom: -30px;

    .flex {
        li {
            width: 300px;
            text-align: right;

            .item {
                margin-bottom: 20px;

                .description {
                    margin-bottom: 15px;
                }

                .thumb_5x4 {
                    padding-bottom: 68.5%;
                }
            }

            &:nth-child(1) {
                .description {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 4;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    height: 96px;

                    @media (max-width: 991px) {
                        height: 86px;
                    }

                    @media (max-width: 576px) {
                        height: 100%;
                        text-align: left;
                    }
                }
            }

            &:nth-child(2) {
                width: calc(100% - 300px);
                padding-left: 20px;

                .item {
                    &:nth-child(1) {
                        .description {
                            display: none;
                        }
                    }

                    &:nth-child(2) {
                        display: flex;
                        flex-wrap: wrap;

                        .img {
                            width: 50%;
                            order: 1;
                            padding-right: 20px;
                        }

                        .description {
                            width: 50%;
                            order: 2;
                            text-align: left;
                        }

                        @media (max-width: 991px) {
                            .img {
                                width: 100%;
                                padding-right: 0;
                                margin-bottom: 15px;

                                .thumb_5x4 {
                                    padding-bottom: 62%;
                                }
                            }

                            .description {
                                width: 100%;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                -webkit-line-clamp: 6;
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                                height: 135px;
                                text-align: justify;
                                margin-bottom: 0;
                            }
                        }

                        @media (max-width: 767px) {
                            .img {
                                .thumb_5x4 {
                                    padding-bottom: 80%;
                                }
                            }
                        }
                    }
                }
            }
        }

        @media (max-width: 1140px) {
            li {
                .item {
                    .thumb_5x4 {
                        padding-bottom: 109%;
                    }
                }
            }
        }

        @media (max-width: 767px) {
            li {
                width: 250px;

                &:nth-child(2) {
                    width: calc(100% - 250px);
                }
            }
        }

        @media (max-width: 576px) {

            li {
                width: 100%;
                order: 2;

                &:nth-child(2) {
                    width: 100%;
                    padding-left: 0;
                    order: 1;
                }

                .item {
                    .description {
                        text-align: justify;
                        display: block !important;
                    }
                }
            }
        }
    }

    @media (max-width: 576px) {

        .title-cate {
            text-align: left;
        }

        .item {
            margin-bottom: 30px;
        }

        .description {
            margin-bottom: 24px;
        }
    }
}

.section_contact {
    padding: 30px 0;
    background: url(images/graphics/hoa-sen.png) no-repeat top;
}

.form-default {
    &.min-width {
        max-width: 546px;
        margin: 0 auto;
    }

    .form-control {
        border: 1px solid #FFDFA4;
        border-radius: 2px;
        color: #FFDFA4;
        background: none;
        padding: 15px;
        height: 50px;

        @include placeholder {
            color: #FFDFA4;
        }
    }

    textarea.form-control {
        height: 100px;
    }

    .btn {
        width: 100%;
        background: #FFDFA4;
        color: #000000;
        font-size: 20px;
        border-radius: 2px;
        padding: 9px
    }
}

.hide-pc {
    display: none
}

@media (max-width: 576px) {
    .hide-mobile {
        display: none
    }

    .hide-pc {
        display: block
    }
}