
.footer{
    background: url(images/graphics/bg-footer.png);
    background-size: cover;
    padding: 30px 48px;
    position: relative;
    font-size: 18px;
    color: var(--orange);
    &::before{
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(5, 5, 5, 0.6);
    }
    .logo,.address{
        position: relative;z-index: 2;
    }
    .social{
        text-align: right;
        padding-bottom: 15px;
        margin-bottom: 10px;
        border-bottom: 1px solid #FFDFA4;
        a{
            color: var(--orange);
            margin-left: 15px;font-size: 20px;
        }
    }
    @media (max-width: 767px) {
        font-size: 16px;
        .logo{
            width: 100%;
            text-align: center;margin-bottom: 20px;
            img{width: 100px;}
        }
        .address{
            width: 100%;
            text-align: center;
        }
        .social{
            text-align: center;
        }
    }
}